/* Sidebar when collapsed */
.sidebar-admin.collapsed{
  width: 80px; /* Sidebar width when collapsed */
}

.sidebar-admin.collapsed ~ .admin-right {
  margin-left: 80px;
}

/* Toggle button for collapsing the sidebar */
.sidebar-toggle {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.toggle-btn {
  background: none;
  border: none;
  color: #ecf0f1;
  font-size: 20px;
  cursor: pointer;
}

/* Default sidebar styles */
.sidebar-admin {
  background: linear-gradient(90deg, #006489 0%, #00AEEF 100%);
  color: white;
  width: 240px;
  overflow-y: auto;
  overflow-x: hidden;
  height: calc(100vh - 12vh);
  transition: width 0.3s ease;
  position: fixed;
  top: 8vh;
  left: 0;
  z-index: 1000;
}

/* Sidebar heading */
.sidebar-heading-admin {
  display: flex;
  justify-content: flex-start;
  /* background: linear-gradient(90deg, #006489 0%, #00AEEF 100%); */
  color: white;
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  padding: 2vh;
  transition: opacity 0.3s ease;
  margin-bottom: 0;
  /* border-bottom: 1px solid #ffffff; */
}

.heading-admin {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid #ffffff;
}

/* Menu list styles */
.menu-list-admin {
  list-style-type: none; /* Remove bullet points */
  padding: 0;
  margin: 0;
}

.admin-layout {
  display: flex;
}

/* Admin content area */
.admin-right {
  flex-grow: 1;
  padding: 20px;
  margin-left: 240px;
  margin-top: 8vh;
  transition: margin-left 0.3s ease;
}

/* Menu item container */
.menu-item-container-admin {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  background: linear-gradient(90deg, #006489 0%, #00AEEF 100%);
}

/* Menu item style */
.menu-item-admin {
  background: transparent;
  color: white;
  width: 100%;
  border: none;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 5px 10px;
  transition: background-color 0.3s ease, border-radius 0.3s ease; /* Smooth transition */
}

.submenu-item-admin {
  background: transparent;
  color: white;
  border: none;
  text-align: right;
  padding: 15px 8px 15px 15px;
  display: flex;
  align-items: center;
  list-style: none;
  transition: background-color 0.3s ease, border-radius 0.3s ease; /* Smooth transition */
}

/* Icon inside menu item */
.menu-item-admin .menu-icon {
  margin-right: 5px;
  color: white;
  font-size: 16px;
}

.menu-icon-title-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.menu-item-admin:hover,
.submenu-item-admin:hover,
.menu-item-admin.active,
.submenu-item-admin.active {
  background-color: #0D6EFD;
  border-radius: 0 30px 30px 0;
}

.menu-title{
  font-size: 14px;
}

.dropdown-icon {
  margin-left: auto;
  display: flex;
  align-items: center;
  font-size: 1rem;
  color: white;
}

/* Sidebar collapsed state styles */
.sidebar-admin.collapsed .menu-item-admin {
  padding: 1vh;
  color: #2c3e50;
  justify-content: center;
}

.sidebar-admin.collapsed .menu-title {
  display: none; /* Hide text when collapsed */
}

.sidebar-admin.collapsed .menu-icon {
  margin: 0 auto;
}

/* Optional: Transition for collapsing sidebar */
.sidebar-admin.collapsed .sidebar-heading-admin {
  opacity: 0;
  visibility: hidden;
}

.sidebar-admin.collapsed .dropdown-icon{
  margin-left: 0;
  justify-content: center;
}

