  padding: 3px;
  width: 40px;
  height: 40px;
  border: 1px solid #ddd;
  border-radius: 50%;
}

.pagination-arrow:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.pagination-number {
  width: 40px;
  height: 40px;
  padding: 10px;
  border: 1px solid #ddd;
  cursor: pointer;
  border-radius: 50%;
}

.pagination-number.active {
