.detail-blog-left{
  width: 25%;
  height: 95vh;
  display: flex;
  flex-direction: column;
  padding: 2vh;
}

.detail-blog-left h3 {
  font-weight: bold;
  margin-bottom: 15px;
  text-transform: uppercase;
  font-size: 16px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}

.recent-post-item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  margin-bottom: 20px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.recent-post-item:hover {
  transform: translateY(-2px);
}

.recent-post-img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  object-position: left;
  border-radius: 6px;
}

.recent-post-text {
  display: flex;
  flex-direction: column;
}
.recent-post-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
}
.recent-post-date-icon {
  font-size: small;
  color: #00AEEF;
}

.recent-post-date {
  font-size: small;
  font-weight: 400;
  color: #000000;
  margin: 0;
}

.recent-post-title {
  font-size: small;
  font-weight: 600;
  color: #0E2A46;
  margin: 4px 0 0 0;
  line-height: 1.2;
}
@keyframes blog-details-skeleton-loading {
  0% {
    background-color: #e3e3e3;
  }
  100% {
    background-color: #f5f5f5;
  }
}

.blog-details-skeleton-card {
  background: linear-gradient(90deg, #e3e3e3 25%, #f0f0f0 50%, #e3e3e3 75%);
  background-size: 400% 100%;
  animation: blog-details-skeleton-loading 1.5s infinite linear;
  border-radius: 12px;
  margin-bottom: 1rem;
}

.blog-details-skeleton-card.large {
  width: 100%;
  height: 350px;
  border-radius: 16px;
}

.recent-post-skeleton {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 16px;
}

.recent-skeleton-image {
  width: 80px;
  height: 80px;
  border-radius: 8px;
  background: linear-gradient(90deg, #e0e0e0 25%, #f5f5f5 50%, #e0e0e0 75%);
  background-size: 100%;
  animation: recent-skeleton-loading 1.5s infinite linear;
}

.recent-skeleton-text {
  height: 14px;
  border-radius: 4px;
  background: linear-gradient(90deg, #e0e0e0 25%, #f5f5f5 50%, #e0e0e0 75%);
  background-size: 200% 100%;
  animation: recent-skeleton-loading 1.5s infinite linear;
}

.recent-skeleton-text.title {
  width: 80%;
  height: 14px;
}

.recent-skeleton-text.subtitle {
  width: 60%;
  height: 12px;
}

@keyframes recent-skeleton-loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

